<template>
    <a-form @submit="handleSubmit"
            :autoFormCreate="(form)=>{this.form = form}">
        <a-form-item
                label='会议议题'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="title"
        >
            <a-input/>
        </a-form-item>
        <a-form-item
                label='会议类型'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                :fieldDecoratorOptions="{valuePropName:'checked'}"
                fieldDecoratorId="type"
        >
            <a-radio-group buttonStyle="solid">
                <a-radio-button :value="1">线下会议</a-radio-button>
                <a-radio-button :value="2">线上会议</a-radio-button>
            </a-radio-group>
        </a-form-item>
        <a-form-item
                label='会议地点'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="location"
        >
            <a-input/>
        </a-form-item>
        <a-form-item
                label='参会领导'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="leader_ids"
        >
            <SelectUsers/>
        </a-form-item>
        <a-form-item
                label='参会人员'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="attend_user_ids"
        >
            <SelectUsers/>
        </a-form-item>
        <a-form-item
                label='审批领导'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="audit_user_id"
        >
            <SelectUser/>
        </a-form-item>
        <a-form-item
                label='会议时间'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="opened_at"
        >
            <DateTime/>
        </a-form-item>
        <a-form-item
                v-if="false"
                label='附件'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="more_files"
        >
            <upload-files
                    :defaultFileList="filesDefaultFileList"
                    @change="files=>{
                          this.form.setFieldsValue({
                            more_files: files,
                        })
                    }"
                    :initFiles="[]"/>
        </a-form-item>
        <a-form-item
                :wrapperCol="{ span: 12, offset: 5 }"
        >
            <a-button type='primary' htmlType='submit'>
                提交
            </a-button>
        </a-form-item>
    </a-form>
</template>

<script>
    import {getMeeting, createMeeting, updateMeeting} from '@/api/meeting'
    import SelectUser from '@/components/select/SelectUser'
    import SelectUsers from '@/components/select/SelectUsers'
    import DateTime from '@/components/select/DateTime'
    import uploadFiles from '@/components/upload/UploadFiles'
    import {array_column} from '@/libs/tools'

    export default {
        components: {
            SelectUser, SelectUsers, uploadFiles, DateTime
        },
        props: {
            id: {
                type: Number,
                default: 0,
            }
        },
        data() {
            return {
                log: console.log,
                form: {},
            }
        },
        computed: {},
        methods: {
            init() {
                if (!this.id) {
                    return;
                }

                getMeeting(this.id)
                    .then(res => {
                        this.form.setFieldsValue({
                            title: res.data.title,
                            type: res.data.type,
                            location: res.data.location,
                            leader_ids: res.data.meeting_leader_ids,
                            attend_user_ids: res.data.meeting_attend_user_ids,
                            audit_user_id: res.data.audit.audit_user_id,
                            opened_at: res.data.opened_at,
                            // more_files: res.data.more.hasOwnProperty('files') ? res.data.more.files : null,
                        })
                    })
            },
            beforeSubmit() {
                return true;
                let res = false
                // this.form.validateFields(
                //     (err) => {
                //         if (!err) {
                //             res = true
                //         }
                //     })
                return res
            },
            handleSubmit(e) {
                e.preventDefault();

                let request;

                let data = this.form.getFieldsValue();

                if (this.id) {
                    request = updateMeeting(this.id, data)
                } else {
                    request = createMeeting(data)
                }
                request.then(() => {
                    let msg = this.id ? '更新成功' : '新增成功';
                    this.$message.success(msg);
                })
            },
        },
        mounted() {
            this.init();
        },
    }
</script>
